import React from 'react'
import {Avatar} from '@material-ui/core'
import a from './a.jpg'
import { ImLocation } from 'react-icons/im'
import { AiTwotoneMail } from 'react-icons/ai'
import { AiTwotonePhone } from 'react-icons/ai'
import { CgProfile } from 'react-icons/cg'

const Left = () => {
    return (
        <div className='left'>
            <Avatar className='avatar' style={{ height: '140px', width: '140px' }} src={a} size='50px'/>
            <div className='contact'>
                <br/>
                
                <h1>Contact</h1>
                <br/>
                <hr/>
                <h> <ImLocation color='#0d1a0b'/> <p1> Dhaka, Bangladesh </p1></h>
                <br/>
                <h> <AiTwotoneMail color='#0d1a0b'/> <a href='https://mail.google.com/mail/u/0/' target='_blank'>tanvirrezaanik@gmail.com </a></h>
                <br/>
                <h> <AiTwotonePhone color='#0d1a0b'/> <p1>+88 01687005154 </p1></h>
                <br/>
                <br/>
                <h> <CgProfile color='#0d1a0b'/> <a href='https://anikreza.github.io/portfolio2/' target='_blank'> Portfolio Website </a></h>
                <br/>
                <hr/>
            </div>
        </div>
    )
}

export default Left
