import React from 'react'
import { ImPencil2 } from 'react-icons/im'


const Right = () => {
    return (
        <div className='right'>
            <br/>
            <div className='header-right'>
                  <h2><ImPencil2/> <p1>Cover Letter</p1></h2>
            </div>
            <div className='date'>
                   <p1>27th August 2021</p1>
            </div>
            <br/><br/><br/>
            <div className='letter'>
                  <p>
                      Dear mr X,
                      <br/><br/>
                      I'm writting this because of my passionate interest and desire of joining the web developer team of your company,
                      REVE Systems Limited. 
                      <br/> <br/>
                      Like thousands of other freshers, I only have one valid source to prove to you that I can be the best fit to the position 
                      I'm seeking, and that is, my projects so far! You can look upon them through my portfolio website attached.

                      <br/><br/>
                      I have researched the whereabouts of REVE Systems a bit, and thought to  myself, look, this is a place I could have been dreaming of to start developing my career! 
                      one of your organizational focus is Enterprise Communication, and that made me real excited, as I've always been curious about text and multimedia transmission, 
                      even worked out a texting application during the corridor of post-graduation, job ready period. API based WebRTC could be one of my strongsuites too, if I'm given the chance! 


                      <br/><br/>
                      I'm someone who works his skin of until its done, and quick to catch up with things, specially technical things! Hence, I can confidently say, 
                      I would desperately grab the opportunity if it is on to prove my worth finaly after all those years of education.  

                      <br/> <br/>
                      So, I'll be waiting for a nudge, and you can then verify eventually, if this kid is good enough to join REVE systems, which I absolutely think I am!      
                  </p>
                  <br/><br/>
                  <p1>
                      Best regards! <br/>
                      Tanvir Reza Anik
                  </p1>
                  <br/> <br/><br/> <br/>
            </div>
        </div>
    )
}

export default Right
